import Vue from 'vue'
import App from './Website.vue'

import VueWechatTitle from 'vue-wechat-title'

Vue.use(VueWechatTitle)

import './quasar'
import './vuetify'
import './modules/app'
import './modules/common'
import './modules/requests'
import './modules/forum'
import './modules/ads'
import store from './modules/store'
import base from './modules/base'
Vue.use(base)


import router from './router/website'
import itemmanghuV from './components/items/manghua_v'
import itemmanghuROW from './components/items/manghua_row'
import header from './components/header'
import footer from './components/footer'
import vuetify from './plugins/vuetify';

import './assets/css/theme.css'
import './assets/mobile/app.css'

Vue.config.productionTip = false

Vue.component('itemmanghuROW', itemmanghuROW)
Vue.component('item_manghu_v', itemmanghuV)
Vue.component('appheader', header)
Vue.component('appfooter', footer)



window.ga = new Vue({
  render: h => h(App),
  vuetify,
  router: router,
  store,
  created() {
    this.$store.dispatch('init')
    this.init()
  }
}).$mount('#app')


